import React from 'react'
import './header.css'

export default class RmdHeader extends React.Component {
  render() {
    return (
      <div styleName="header-warpper">
        <span styleName="title">{ this.props.title }</span>
        {/* {this.props.title === '' ? '' : <span styleName="more"></span>} */}
      </div>
    )
  }
}



