import React from 'react'
import './detail.css'

import { Header, Footer } from 'components/common'
import { TeacherContent, StudentContent, TeacherContentb } from 'components/detail'

export default class Detail extends React.Component {
  render() {
    let Nav = null
    let MainContent = null
    const pathname = this.props.location.pathname
    switch (pathname.split('/')[1]) {
      case 'teacher':
        MainContent = <TeacherContent />
        break
      case 'teacherb':
        MainContent = <TeacherContentb />
        break
      case 'student':
        const id = pathname.split('/')[2]
        MainContent = <StudentContent id={id} />
        break
      default:
        MainContent = null
        break
    }
    return (
      <div styleName="page-wrapper">
        <div styleName="header-wrapper">
          <Header />
        </div>
        <div styleName="mid-container" className="clearfix">
          <div styleName="nav-container">
            {Nav}
          </div>
          <main styleName="left-container">
            {MainContent}
          </main>
        </div>
        <div styleName="footer-wrapper">
          <Footer />
        </div>
      </div>
    )
  }
}



