import React from 'react'
import './detailOptions.css'
import { withRouter } from 'react-router-dom'

@withRouter
export default class DetailOptions extends React.Component {
  render() {
    return (
      <div>
        <div styleName="content">
          {this.props.intro}
        </div>
      </div>
    )
  }
}



