import React from 'react'
import { Divider } from 'antd'
import './studentContent.css'
import StudentCard from 'components/detail/student/student-card/StudentCard'
import Introduction from './introduction/Introduction'
import { DetailOptions } from '../common'

import studentsData from 'src/ajax/fe-data/students.json'
var studata = studentsData['data']

export default class StudentContent extends React.Component {
  render() {
    var sid = this.props.id
    var student = {}
    studata.forEach(function(item) {
      if (item['_id']['$oid'] === sid) {
        student = item
      }
    })
    const intro = (
      <div>
        <Divider><span styleName="divider"></span></Divider>
        <Introduction introduction={student.introduction} />
        <Divider><span styleName="divider"></span></Divider>
        <Introduction introduction={student.skill} />
      </div>
    )
    return (
      <div>
        <StudentCard stuInfo={student} />
        <DetailOptions type="student" intro={intro} />
      </div>
    )
  }
}



